.catalog__item {
  position: relative;
  display: flex;
  background-color: #fff;
  border: 1px solid #e6e6e6;

  .tip {
    position: absolute;
    top: 10px;
    left: 10px;

    @include lg-screen {
      font-size: 12px;
    }
  }

  @include sm-screen {
    flex-flow: column;
  }

  &:not(:last-child) {
    margin-bottom: 13px;
  }

  &-img {
    position: relative;
    @include ratio(420, 280);
    max-width: 42.5%;
    height: 100%;
    width: 100%;

    @include sm-screen {
      max-width: 100%;
    }

    img {
      @include coverdiv;
      @include img-cover;
    }
  }

  &-description {
    display: flex;
    justify-content: space-between;
    padding: 27px 32px 36px;

    @include xl-screen {
      flex-flow: column;
      padding: 21px 18px 30px;
    }
  }

  &-column {
    display: flex;
    flex-flow: column;
    max-width: 60.7%;
    width: 100%;

    @include xl-screen {
      max-width: 100%;
    }

    &_price {
      max-width: 32.5%;
      width: 100%;
      padding-top: 17px;

      @include xl-screen {
        padding-top: 33px;
        max-width: 100%;
        flex-flow: row;
        justify-content: space-between;
      }

      @include r(1279px) {
        flex-flow: column;
      }

      @include sm-screen {
        padding-top: 15px;
      }
    }
  }

  .dot {
    width: 2px;
    height: 2px;
  }

  &-code {
    margin-bottom: 7px;
    font-family: $jura;
    font-size: 10px;
    line-height: 1.1;
    letter-spacing: -0.02em;
    color: #898989;
    text-transform: uppercase;
  }

  &-name {
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: -0.03em;

    @include xl-screen {
      font-size: 13px;
      line-height: 16px;
    }
  }

  &-characteristic {
    margin-top: auto;

    @include xl-screen {
      margin-top: 17px;
    }

    @include sm-screen {
      display: none;
    }
  }

  &-buttons {
    margin-top: auto;

    @include xl-screen {
      max-width: 170px;
      width: 100%;
    }

    @include r(1279px) {
      max-width: 100%;
      margin-top: 14px;
    }

    .ui-button {
      height: 42px;

      @include sm-screen {
        font-size: 11px;
      }

      &--dark {
        margin-bottom: 9px;
      }
    }
  }
}