.footer {
  margin-top: 67px;
  background-color: #1E1E1E;
  color: #f0f0f0;
  border-top: 1px solid #F9F9F9;

  &-head {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F9F9F9;
  }

  &-logo {
    padding: 20px 22px;
    width: 26.2%;
    border-right: 1px solid #F9F9F9;

    @include r(1024px) {
      border-right: none;
      width: initial;
    }
  }

  &__nav {
    display: flex;
    width: 66.2%;

    @include r(1024px) {
      display: none;
    }

    &-item {
      width: calc(100% / 3);
      height: 100%;
      padding: 20px 0 20px 7%;

      @include lg-screen {
        padding: 20px 0 20px 4%;
      }

      &:not(:last-child) {
        border-right: 1px solid #F9F9F9;
      }
    }

    &-link {
      display: initial;
      color: #f0f0f0;

      @include lg-screen {
        font-size: 10px;
      }
    }
  }

  &-content {
    display: flex;
    padding: 52px 0;

    @include lg-screen {
      padding: 36px 0;
    }

    @include r(1024px) {
      flex-flow: column;
    }
  }

  &__social {
    width: 26.2%;

    &-title {
      font-family: $jura;
      font-size: 18px;
      line-height: 1.25;
      letter-spacing: -.05em;
      margin-bottom: 9px;
      color: #f0f0f0;
    }

    .social {
      &__link {
        border-color: #f0f0f0;

        .icon {
          color: #f0f0f0;
        }
      }
    }
  }

  &__links {
    display: flex;
    width: 100%;

    @include r(1024px) {
      margin-top: 25px;
    }

    &-column {
      display: flex;
      flex-flow: column;
      width: calc(100% / 3);
      padding-left: 7%;

      @include r(1024px) {
        padding-left: 0;
      }

      &_address {
        width: 45%;
      }
    }

    .ui-link {
      max-width: max-content;
      font-family: $jura;
      font-size: 18px;
      line-height: 1.25;
      margin-bottom: 20px;
      color: #f0f0f0;

      @include lg-screen {
        font-size: 14px;
        margin-bottom: 8px;
      }
    }
  }

  &__address {
    margin-top: 30px;

    &-item {
      font-family: $jura;
      font-size: 18px;
      line-height: 1.25;
      margin-bottom: 12px;

      @include lg-screen {
        font-size: 14px;
        margin-bottom: 8px;
      }
    }
  }

  &-bottom {
    display: flex;
    align-items: center;
    padding: 22px 0;
    font-family: $jura;
    font-size: 12px;
    line-height: 1.6;
    letter-spacing: -0.05em;
    border-top: 1px solid #F9F9F9;

    @include sm-screen {
      flex-flow: column;
      align-items: flex-start;
      margin-bottom: 100px;
    }
  }

  &__copyright {
    width: 18%;

    @include sm-screen {
      width: 100%;
      margin-bottom: 25px;
    }
  }

  &__info {
    @include sm-screen {
      margin-bottom: 25px;
    }
  }

  .privacy {
    margin-left: auto;
    height: 100%;
    color: #f0f0f0;

    @include sm-screen {
      margin-left: 0;
    }
  }
}