.breadcrumbs {
  margin-bottom: 23px;
  &-row {
    display: flex;
    align-items: center;
  }

  &-divider {
    font-family: $jura;
    color: #959595;
  }

  &-item {
    margin-right: 11px;
    font-family: $jura;
    font-size: 10px;
    color: #959595;

    &:not(:first-child) {
      margin-left: 11px;
    }
  }
}