.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 20;
  background-color: #f0f0f0;

  @include sm-screen {
    height: 62px;
  }

  &__container {
    height: 100%;

    @include sm-screen {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__mobile-menu {
    display: none;
    margin-right: 25px;
    width: 30px;
    height: 30px;
    align-items: center;

    @include xxs-screen {
      margin-right: 10px;
      width: 24px;
    }

    .catalog-link-icon {
      margin-right: 0;

      .line {
        background-color: $textDark;
      }
    }

    @include sm-screen {
      display: flex;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #383838;
    height: 100%;

    @include sm-screen {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__left {
    width: 100%;
  }

  &__right {
    justify-content: flex-end;
    max-width: 317px;
    width: 100%;
    border-left: 1px solid #383838;
    padding: 0 13px 0 34px;

    @include sm-screen {
      max-width: 132px;
      padding: 0 0 0 17px;
    }
  }

  &__item {
    border-right: 1px solid #383838;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 10px;
    width: 100%;
    max-width: 167px;

    @include lg-screen {
      max-width: 140px;
    }

    @include sm-screen {
      max-width: 190px;
      border-right: none;
      display: flex;
      align-items: center;
    }

    .logo {
      position: relative;
      @include ratio(108, 14.4);
      max-width: 108px;
      width: 100%;

      @include lg-screen {
        max-width: 108px;
        width: 100%;
      }

      img {
        @include coverdiv;
        @include img-cover;
      }
    }
  }

  &__catalog {
    max-width: 188px;
    width: 100%;

    @include lg-screen {
      max-width: 148px;
    }

    @include sm-screen {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 62px;
      max-width: 100%;
      border-right: none;
      z-index: 10;
      background-color: #f0f0f0;
      box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.07);
    }
  }

  &__navigation {
    display: flex;
    padding-left: 54px;

    @include xl-screen {
      padding-left: 27px;
    }

    @include r(1300px) {
      display: none;
    }

    a {
      color: $textDark;
      transition: opacity 0.25s ease;

      @include xl-screen {
        font-size: 12px;
      }

      &:hover {
        opacity: 0.75;
      }

      &:not(:last-child) {
        margin-right: 35px;

        @include xl-screen {
          margin-right: 25px;
        }
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include sm-screen {
      justify-content: flex-end;
    }
  }

  &__search {
    border: none;

    @include sm-screen {
      display: none;
    }

    .icon {
      font-size: 22px;
    }
  }

  &__profile {
    position: relative;
    border: none;
    color: $textDark;

    @include sm-screen {
      display: none;
    }

    .icon {
      font-size: 22px;
    }

    &-value {
      position: absolute;
      top: -9px;
      right: -5px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: $textDark;
      font-family: 'Inter', sans-serif;
      color: #fff;
      font-size: 11px;
    }
  }
  
  &__cart {
    padding: 14px;
    border: 1px solid #383838;
    border-radius: 30px;
    transition: background-color 0.2s ease;
    font-size: 12px;
    color: $textDark;

    @include sm-screen {
      padding: 10px;
      font-size: 10px;
    }

    &:hover {
      color: #fff;
      background-color: $textDark;
    }
  }
}