.basket {
  &__inner {
    display: flex;
    justify-content: space-between;

    @include r(916px) {
      flex-flow: column;
    }
  }

  &__list {
    max-width: 66.4%;
    width: 100%;

    @include lg-screen {
      max-width: 69%;
    }

    @include r(916px) {
      max-width: 100%;
    }
  }

  &__remove {
    position: absolute;
    right: 18px;
    @include centerer-y;
    font-size: 18px;
    color: $accent;

    @include sm-screen {
      top: 10px;
      right: 10px;
      transform: none;

      .icon {
        font-size: 14px;
      }
    }
  }

  &__list {
    &-head {
      display: flex;
      background-color: #FFFFFF;
      border: 1px solid #E2E2E2;
      font-family: $jura;

      @include sm-screen {
        display: none;
      }
    }

    &-item {
      font-weight: 700;
      padding: 17px 0;
      font-size: 14px;
      letter-spacing: -0.02em;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 30px 0;

    @include sm-screen {
      border-top: 1px solid #E2E2E2;
      flex-flow: column;
      align-items: flex-start;
      padding: 20px 15px;
    }

    &:not(:first-child) {
      border-top: 1px solid #E2E2E2;
    }

    .select {
      &__head {
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        height: 42px;
      }
    }

    .price {
      &-new {
        font-size: 8px;

        .value {
          font-size: 12px;
        }
      }
    }

    &-title {
      display: none;

      @include sm-screen {
        display: block;
        font-family: $jura;
        width: 35%;
        font-size: 16px;
        margin-right: 15px;
      }
    }

    &-info {
      display: flex;
      align-items: center;

      @include lg-screen {
        flex-flow: column;
        align-items: flex-start;
      }

      @include sm-screen {
        flex-flow: row;
        align-items: center;
        margin-bottom: 20px;
      }
    }

    &-name {
      margin-left: 25px;
      font-family: $jura;
      font-size: 14px;
      line-height: 1.25;
      letter-spacing: -0.02em;

      @include lg-screen {
        margin-left: 0;
        margin-top: 11px;

        br {
          display: none;
        }
      }

      @include sm-screen {
        margin-left: 11px;
        margin-top: 0;
      }
    }

    &-img {
      position: relative;
      @include ratio(138, 78);
      max-width: 138px;
      width: 100%;
      border: 1px solid #EAEAEA;

      @include lg-screen {
        max-width: 85px;
      }

      @include sm-screen {
        max-width: 138px;
      }

      @include xs-screen {
        max-width: 85px;
      }

      img {
        @include coverdiv;
        @include img-cover;
      }
    }
  }

  &__info {
    padding-left: 23px;
    width: 45%;

    @include lg-screen {
      width: 30%;
    }

    @include sm-screen {
      padding-left: 0;
      width: 100%;
    }
  }

  &__price,
  &__count,
  &__total {
    width: calc(55% / 3);

    @include lg-screen {
      width: calc(70% / 3);
    }

    @include sm-screen {
      width: 100%;
      margin-bottom: 21px;
    }

    @include sm-screen {
      display: flex;
      align-items: center;
    }

    .price-new {
      @include sm-screen {
        margin-bottom: 0;
      }
    }
  }

  &__body {
    border: 1px solid #E2E2E2;
    border-top: none;
  }
}