.modal-profile {
  .modal__inner {
    max-width: 630px;
    width: 100%;
    padding: 0;
    border-radius: 0;
  }

  .modal__close {
    position: absolute;
    top: 0;
    right: -40px;

    @include sm-screen {
      top: -30px;
      right: 0;
    }

    .icon {
      color: #fff;
    }
  }

  &__tabs {
    .tabs {
      &__head {
        display: flex;
        border-bottom: 1px solid #262626;
      }

      &__item {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 74px;
        font-weight: 700;
        color: rgba(0, 0, 0, 0.5);
        transition: color 0.25s ease;

        @include sm-screen {
          font-size: 10px;
        }

        &.is-active {
          color: rgba(38, 38, 38, 1);
        }

        &:not(:first-child) {
          border-left: 1px solid #262626;
        }
      }

      &__body {
        margin: 0 auto;
        padding: 50px 15px;
        max-width: 450px;
        width: 100%;

        @include sm-screen {
          padding: 20px 15px 50px;
        }

        .ui-form--login {
          margin-top: 50px;
        }

        .ui-block {
          &:not(:first-child) {
            margin-top: 30px;
          }
        }

        .ui-button {
          margin-top: 20px;
          height: 57px;
        }

        .policy {
          font-family: $jura;
          text-align: center;
          font-size: 12px;
          max-width: 257px;
          margin: 21px auto 0;
        }

        .forget-password__link {
          display: block;
          font-weight: 700;
          color: $textDark;
          width: max-content;
          margin: 100px auto 0;
        }
      }
    }
  }
}